import { Component, OnInit } from '@angular/core';
import { Store,select } from '@ngrx/store'
import { Notification } from 'src/app/store/models/notification';

@Component({
  selector: 'app-notification',
  templateUrl: './notification.page.html',
  styleUrls: ['./notification.page.scss'],
})
export class NotificationPage implements OnInit {

  notifications: any = [];
  theNewNotifications: any = [];
  notification;
  currentDate;
  convertedDate;
  dateDebut;
  dateFin;

  public pageInfo ={ title: 'Notifications', subTitle: ''};
  constructor(private store: Store<{notification: Notification[]}>) { }

  ngOnInit() {
    this.convertedDate = (new Date()).getTimezoneOffset() * 60000;
      let localISOTimeBox = (new Date(Date.now() - this.convertedDate));
      let newDateDebut = new Date(localISOTimeBox.setHours( localISOTimeBox.getHours() -2 )).toISOString().slice(0,-1);
      let newDateFin = new Date(localISOTimeBox.setHours( localISOTimeBox.getHours() +1 )).toISOString().slice(0,-1);

      this.dateDebut = newDateDebut
      this.dateFin = newDateFin

    this.currentDate = new Date();
    this.store.pipe(select('notification')).subscribe(values =>{
      let newNotifications = [];
      newNotifications.splice(1,0,values);
      this.notifications = newNotifications[0];
      console.log(this.notifications)
    });
  }
}
